import { useEffect, useState } from 'react'
import { fetchPortfolios, fetchTickersSimplified } from '../../api/portfolio'
import s from './PortfolioIncludes.module.css'

// Components
import Ticker from '../Ticker/Ticker'

// Images
import AAPL from '../../assets/images/portfolioOverview/AAPL.png'
import KO from '../../assets/images/portfolioOverview/KO.png'
import MSFT from '../../assets/images/portfolioOverview/MSFT.png'
import TGT from '../../assets/images/portfolioOverview/TGT.png'
import TSLA from '../../assets/images/portfolioOverview/TSLA.png'
import WMT from '../../assets/images/portfolioOverview/WMT.png'

interface TickerData {
	id: number
	figi?: string
	img?: string
	companyName?: string
	count: number
	price: number
	profit: number
	types?: number[]
}

interface TickerSimplified {
	id: number
	figi: string
	types: number[]
}

const tickerImages = [
	{ id: 1, img: KO, name: 'Coca Cola Company' },
	{ id: 2, img: TSLA, name: 'Tesla Inc.' },
	{ id: 3, img: AAPL, name: 'Apple Inc.' },
	{ id: 4, img: TGT, name: 'Target Corporation' },
	{ id: 5, img: MSFT, name: 'Microsoft Corporation' },
	{ id: 6, img: WMT, name: 'Walmart Inc.' },
]

const PortfolioIncludes: React.FC = () => {
	const [tickers, setTickers] = useState<TickerData[]>([])

	useEffect(() => {
		const loadData = async () => {
			const portfoliosData = await fetchPortfolios()
			const tickersData = await fetchTickersSimplified()

			const combinedData = portfoliosData.map((portfolio: TickerData) => {
				const tickerDetail = tickersData.find(
					ticker => ticker.id === portfolio.id
				)
				const tickerImage = tickerImages.find(
					image => image.id === portfolio.id
				)

				return {
					...portfolio,
					figi: tickerDetail?.figi ?? '',
					types: tickerDetail?.types ?? [],
					img: tickerImage?.img ?? '',
					companyName: tickerImage?.name ?? '',
				}
			})

			setTickers(combinedData)
		}

		loadData()
	}, [])

	return (
		<div className={s.wrapper}>
			<div className={s.includes_header}>
				<h2>Portfolio includes:</h2>
				<div className={s.includes_btns}>
					<button>All</button>
					<button>Stocks</button>
					<button>Funds</button>
					<button>Futures</button>
					<button>Crypto</button>
					<button>Currencies</button>
					<button>Bonds</button>
				</div>
			</div>
			<div className={s.tickers_list}>
				{tickers.map(ticker => (
					<Ticker key={ticker.id} tickerData={ticker} />
				))}
			</div>
		</div>
	)
}

export default PortfolioIncludes
