import { Link } from 'react-router-dom'
import s from './Ticker.module.css'

interface TickerProps {
	tickerData: {
		id: number
		figi?: string
		img?: string
		companyName?: string
		count: number
		price: number
		profit: number
		types?: number[]
	}
}

const Ticker: React.FC<TickerProps> = ({ tickerData }) => {
	const { id, figi, img, companyName, count, price, profit, types } = tickerData

	const formatNumber = (number: number): string => {
		const formatter = new Intl.NumberFormat('ru-RU')
		return formatter.format(number)
	}

	const formattedTotal = formatNumber(count * price)
	const formattedProfit = formatNumber(profit)

	console.log(types)

	return (
		<div className={s.tickerContainer}>
			<div className={s.imgAndName}>
				<img src={img} alt={companyName} className={s.tickerImage} />
				<div className={s.tickerContent}>
					<div className={s.tickerHeader}>
						<Link to={`/ticker/${id}`} className={s.tickerFigi}>
							{figi}
						</Link>
						<h3 className={s.tickerCompanyName}>{companyName}</h3>
					</div>
					<div className={s.tickerDetails}>
						<p>{`${count} PCS • $ ${price}`}</p>
					</div>
				</div>
			</div>
			<div className={s.tickerFooter}>
				<h2 className={s.tickerTotal}>$ {formattedTotal}</h2>
				<div className={s.tickerProfit}>+ $ {formattedProfit} (1,7%)</div>
			</div>
		</div>
	)
}

export default Ticker
