import s from './TotalValuation.module.css'

import LineChart from '../Chart/Chart'

interface PortfolioIncomeProps {
	dataIncome: {
		valuation: string
		profit: string
	} | null
}

const TotalValuation: React.FC<PortfolioIncomeProps> = ({ dataIncome }) => {
	return (
		<div className={s.portfolio_summary}>
			<div className={s.total_info}>
				<div className={s.header}>
					<p className={s.total_value}>Total valuation</p>
					<h1 className={s.total_price}>{dataIncome?.valuation}</h1>
					<div className={s.income}>
						<p>All time income</p>
						<div className={s.income_profit}>
							<div className={s.income_fix}>{dataIncome?.profit}</div>
							<div className={s.income_percent}>+3%</div>
						</div>
					</div>
					<p className={s.composition_title}>Composition</p>
					<div className={s.tikersTypes}>
						<div className={s.bar}>
							<div
								style={{ width: '90.18px', backgroundColor: '#E9865B' }}
							></div>
							<div
								style={{ width: '83.5px', backgroundColor: '#F3D381' }}
							></div>
							<div
								style={{ width: '80.16px', backgroundColor: '#5076C8' }}
							></div>
							<div
								style={{ width: '33.4px', backgroundColor: '#A2C5EB' }}
							></div>
							<div
								style={{ width: '33.4px', backgroundColor: '#6FC1C1' }}
							></div>
							<div
								style={{ width: '13.36px', backgroundColor: '#96346C' }}
							></div>
						</div>
						<div className={s.infoTypeContainer}>
							<div className={s.infoType}>
								<div className={s.colorAndName}>
									<div
										className={s.colorCube}
										style={{ backgroundColor: '#E9865B' }}
									></div>
									<p className={s.typeName}>Bonds</p>
								</div>
								<p className={s.percent}>27 %</p>
							</div>
							<div className={s.infoType}>
								<div className={s.colorAndName}>
									<div
										className={s.colorCube}
										style={{ backgroundColor: '#F3D381' }}
									></div>
									<p className={s.typeName}>Future</p>
								</div>
								<p className={s.percent}>25 %</p>
							</div>
							<div className={s.infoType}>
								<div className={s.colorAndName}>
									<div
										className={s.colorCube}
										style={{ backgroundColor: '#5076C8' }}
									></div>
									<p className={s.typeName}>Funds</p>
								</div>
								<p className={s.percent}>24 %</p>
							</div>
							<div className={s.infoType}>
								<div className={s.colorAndName}>
									<div
										className={s.colorCube}
										style={{ backgroundColor: '#A2C5EB' }}
									></div>
									<p className={s.typeName}>Crypto</p>
								</div>
								<p className={s.percent}>10 %</p>
							</div>
							<div className={s.infoType}>
								<div className={s.colorAndName}>
									<div
										className={s.colorCube}
										style={{ backgroundColor: '#6FC1C1' }}
									></div>
									<p className={s.typeName}>Currency</p>
								</div>
								<p className={s.percent}>10 %</p>
							</div>
							<div className={s.infoType}>
								<div className={s.colorAndName}>
									<div
										className={s.colorCube}
										style={{ backgroundColor: '#96346C' }}
									></div>
									<p className={s.typeName}>Stocks</p>
								</div>
								<p className={s.percent}>4 %</p>
							</div>
						</div>
					</div>
				</div>
				<div className={s.divider}></div>
			</div>
			<LineChart />
		</div>
	)
}

export default TotalValuation
