import { useEffect, useState } from 'react'
import { fetchPortfolioTitle } from '../../api/portfolio'
import s from './PortfolioOverview.module.css'

// Images

// Components
import PortfolioIncludes from '../../components/PortfolioIncludes/PortfolioIncludes'
import PortfolioHeader from '../../components/PortfolioTitle/PortfolioHeader'
import TotalValuation from '../../components/TotalValuation/TotalValuation'

interface PortfolioTitle {
	name: string
	valuation: string
	profit: string
}

const PortfolioOverview = () => {
	const [portfolioTitle, setPortfolioTitle] = useState<PortfolioTitle | null>(
		null
	)

	useEffect(() => {
		const getData = async () => {
			const fetchedData = await fetchPortfolioTitle()
			setPortfolioTitle(fetchedData)
		}

		getData()
	}, [])

	return (
		<div className={s.portfolio_overview}>
			<div className={`${s.container} container`}>
				<PortfolioHeader data={portfolioTitle} />
				<div className={s.info_and_chart}>
					<TotalValuation dataIncome={portfolioTitle} />
					{/* <div className={s.chart}></div> */}
				</div>
				<PortfolioIncludes />
			</div>
		</div>
	)
}

export default PortfolioOverview
