import s from './PortfolioHeader.module.css'

// Images
import more from '../../assets/images/portfolioOverview/more.png'
import refresh from '../../assets/images/portfolioOverview/refresh.png'

interface PortfolioHeaderProps {
	data: {
		name: string
	} | null
}

const PortfolioHeader: React.FC<PortfolioHeaderProps> = ({ data }) => {
	return (
		<div className={s.portfolio_header}>
			<div className={s.portfolio_header__text}>
				<h1>{data?.name}</h1>
				<p>Last updated: Yesterday, 12:45</p>
			</div>
			<div className={s.portfolio_header__btns}>
				<button className={`${s.btn_refresh} ${s.btns_header}`}>
					<img src={refresh} alt='refresh' />
					<p>Refresh data</p>
				</button>
				<button className={`${s.btn_more} ${s.btns_header}`}>
					<img src={more} alt='more' />
				</button>
			</div>
		</div>
	)
}

export default PortfolioHeader
