import axios from 'axios'

const URL: string =
	'https://my-json-server.typicode.com/QuantityProject/test_assignment/'

interface Total {
	profit: number
	valuation: number
}

export interface TickerData {
	id: number
	count: number
	price: number
	profit: number
}

interface Portfolio {
	id: number
	name: string
	total: Total
	tickers_data: TickerData[]
}

interface PortfolioTitle {
	name: string
	profit: string
	valuation: string
}

export async function fetchPortfolioTitle(): Promise<PortfolioTitle | null> {
	try {
		const response = await axios.get<Portfolio[]>(`${URL}portfolios`)
		if (response.data && response.data.length > 0) {
			const portfolio = response.data[0]
			const name =
				portfolio.name.charAt(0).toUpperCase() + portfolio.name.slice(1)
			const profit = `$ ${portfolio.total.profit.toLocaleString('en-US')}`
			const valuation = `$ ${portfolio.total.valuation.toLocaleString('en-US')}`

			return {
				name,
				profit,
				valuation,
			}
		} else {
			console.log('Портфолио не найдено')
			return null
		}
	} catch (error) {
		console.error('Ошибка при получении данных портфолио:', error)
		return null
	}
}

export const fetchPortfolios = async (): Promise<TickerData[]> => {
	try {
		const response = await axios.get<Portfolio[]>(`${URL}portfolios`)
		const tickersData = response.data[0]?.tickers_data || []
		return tickersData
	} catch (error) {
		console.error('Ошибка при получении данных портфолио:', error)
		return []
	}
}

interface Ticker {
	id: number
	figi: string
	company?: number
	types: number[]
}

export const fetchTickersSimplified = async (): Promise<Ticker[]> => {
	try {
		const response = await axios.get<Ticker[]>(
			'https://my-json-server.typicode.com/QuantityProject/test_assignment/tickers'
		)
		const simplifiedData = response.data.map(ticker => ({
			id: ticker.id,
			figi: ticker.figi,
			types: ticker.types,
		}))
		return simplifiedData
	} catch (error) {
		console.error('Ошибка при получении данных тикеров:', error)
		return []
	}
}

interface TickerType {
	id: number
	name: string
}

export const fetchTickersTypes = async (): Promise<TickerType[]> => {
	try {
		const response = await axios.get<TickerType[]>(`${URL}ticker_types`)
		return response.data
	} catch (error) {
		console.error('Ошибка при получении данных тикеров:', error)
		return []
	}
}
